import { environment } from "./../../environments/environment";
import * as moment from "moment";
import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { HttpClient } from "@angular/common/http";
import { MDBSpinningPreloader } from "../../typescripts/pro/preloader/preloader.service";

import { Task } from "../shared/task/task";
import { User } from "../shared/user/user";

@Component({
  selector: "app-feedback",
  templateUrl: "./feedback.component.html",
  styleUrls: ["./feedback.component.scss"]
})
export class FeedbackComponent implements OnInit {
  public userUrl = environment.corona.USER_BASE_URL;
  public taskUrl = environment.corona.TASK_BASE_URL;
  public ratingUrl = environment.corona.RATING_BASE_URL + "/rating";

  public query: string;
  public ids: object;
  public ratingText: string;
  public task: Task;
  public assignee: User;
  public rated: boolean;
  public ratingFound: boolean;
  public rating: any;
  public feedbackType: string;
  public feedbackMessage: string;

  constructor(
    private http: HttpClient,
    private router: Router,
    private route: ActivatedRoute,
    private mdbSpinningPreloader: MDBSpinningPreloader
  ) {
    this.ids = {};
    this.ratingText = "";
    this.task = new Task();
    this.assignee = new User();
    this.rated = false;
    this.ratingFound = false;
    this.feedbackType = "";
    this.feedbackMessage = "";
  }

  ngOnInit() {
    this.mdbSpinningPreloader.start();
    this.rating = {
      taskId: "",
      rating: 0,
      queryString: "",
      assignee: "",
      additionalInfo: ""
    };

    this.route.queryParams.subscribe(params => {
      const query = params.q;
      this.query = query;

      const splitIds = query.split("--");
      this.ids["task"] = splitIds[0];
      this.ids["assignee"] = splitIds[1];

      this.checkRating(query).subscribe(resp => {
        if (resp["status"]) {
          this.rating = resp["data"][0];
          this.ratingFound = true;
        }
      });

      if (this.ids["task"]) {
        this.getTask(this.ids["task"]).subscribe(
          response => {
            if (response["status"]) {
              const task = response["data"];
              this.task = task[Object.keys(task)[0]];

              this.getUser(this.ids["assignee"]).subscribe(resp => {
                const assignee = resp["data"];
                this.assignee = Object.assign(new User(), assignee);
                this.mdbSpinningPreloader.stop();
              });
            } else {
              // this.router.navigate(["/404"]);
              this.mdbSpinningPreloader.stop();
            }
          },
          err => {
            // this.router.navigate(["/404"]);
            this.mdbSpinningPreloader.stop();
          }
        );
      } else {
        // this.router.navigate(["/404"]);
        this.mdbSpinningPreloader.stop();
      }
    });
  }

  convertDateTime(dateTime, format) {
    if (dateTime === 0 || undefined) {
      return "-";
    }
    return moment(new Date(dateTime)).format(format);
  }

  checkRating(query) {
    return this.http.get(this.ratingUrl + "s?queryString=" + query);
  }

  getUser(id) {
    return this.http.get(this.userUrl + "/user/" + id + "?forRating=true");
  }

  getTask(id) {
    return this.http.get(this.taskUrl + "/task/" + id);
  }

  createRating() {
    this.feedbackMessage = "";
    this.feedbackType = "";
    const rating = {
      ...this.rating,
      taskId: this.ids["task"],
      queryString: this.query,
      assignee: this.assignee["deletedAt"] ? this.assignee.getFullName() : this.ids["assignee"]
    };

    if (this.rating["additionalInfo"].length) {
      rating["additionalInfo"] = this.rating["additionalInfo"];
    } else {
      delete rating["additionalInfo"];
    }

    return this.http.put(this.ratingUrl, rating, { observe: "response" }).subscribe(
      data => {
        if (data.status === 200) {
          this.feedbackType = "success";
          this.feedbackMessage = data.body["message"];
          this.rated = true;
        }
      },
      err => {
        this.feedbackType = "error";
        this.feedbackMessage = err.error["error"];
      }
    );
  }

  changeStar(stars) {
    if (!this.ratingFound) {
      this.rating["rating"] = stars;

      switch (stars) {
        case 1:
          this.ratingText = "Very Poor!";
          break;

        case 2:
          this.ratingText = "Poor";
          break;

        case 3:
          this.ratingText = "Fairly Okay";
          break;

        case 4:
          this.ratingText = "Good Job";
          break;

        case 5:
          this.ratingText = "Excellent";
          break;

        default:
          this.ratingText = "";
          break;
      }
    }
  }
}



